html, body {
  margin: 0;
}

.desc-area {
  margin: 1em;
}

.header {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: .5em 4em;
  margin: 1.3em 0;
  display: flex;
}

.header > h1 {
  margin: 0;
}

.header > ul.social {
  flex-wrap: wrap;
  gap: 8px;
  margin: 0;
  padding: 0;
  display: flex;
}

.header > ul.social > li {
  list-style: none;
}

.header > ul.social img {
  display: block;
}

.oo-area {
  text-align: center;
  background-color: #000;
  flex-direction: column;
  display: flex;
  position: relative;
}

.oo-control {
  transition: opacity .15s ease-in-out;
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
}

:fullscreen .oo-control:not(:hover) {
  opacity: 0;
}

.oo-control button {
  cursor: pointer;
  color: #fff;
  opacity: .75;
  background-color: #0000;
  border: none;
  border-radius: 0;
  padding: .25em;
}

.oo-control button:hover {
  opacity: 1;
  background-color: #ffffff1a;
}

.oo-control button svg {
  fill: currentColor;
  width: 1.8em;
  height: 1.8em;
  display: block;
}

#oo {
  max-width: 100%;
  height: 100vw;
  max-height: 100vh;
  margin: auto;
  display: block;
}

#eye_black {
  transform-origin: 50% 55%;
  transform-box: fill-box;
  transform: none;
}

.rot-clock {
  animation: .75s linear infinite eye_rotate;
}

.rot-anticlock {
  animation: .75s linear infinite reverse eye_rotate;
}

@keyframes eye_rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
